import {
  ArrowLeftOutlined,
  DeleteOutlined,
  DownOutlined,
  SearchOutlined,
  SettingOutlined,
  SyncOutlined,
  InsertRowAboveOutlined,
  SaveOutlined,
  EditOutlined,
} from "@ant-design/icons";
import InfiniteScroll from "react-infinite-scroll-component";
import {
  Col,
  Card,
  Row,
  Form,
  Input,
  Tooltip,
  Typography,
  List,
  Button,
  Dropdown,
} from "antd";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";

const { Title } = Typography;
const CListData = ({
  children,
  onDetail,
  handleSearch,
  refresh,
  data,
  total,
  accessPermission,
  paginate,
  loading,
  onLoadMore,
  handleBack,
  handleRemove,
  items,
  handleInsert,
  isForms,
  handleSubmit,
  id,
  onSetForms,
  onClickDetail,
  labelDetail,
  showTable,
  showTableTogle,
  onTableTogle,
}) => {
  const [menuPath, setMenuPath] = useState("");

  const router = useRouter();
  useEffect(() => {
    let path = router.pathname.slice(1);
    path = path.replaceAll("/[id]", "");
    path = path.replaceAll("/", " / ");
    path = path.replaceAll("-", " ");
    path = path.replaceAll("-", " ");
    let toArray = path.split(" ");

    let toCapitalize = toArray
      .map((word) => {
        return word[0].toUpperCase() + word.substring(1);
      })
      .join(" ");
    setMenuPath(toCapitalize);
  }, []);

  return (
    <Card
      cover={
        <Col
          span={24}
          style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
        />
      }
    >
      <Row>
        <Col span={24}>
          <Title level={3}>{menuPath}</Title>
        </Col>
        <Col span={24} style={{ paddingTop: "20px", marginTop: -10 }}>
          <Card>
            <Row gutter={15}>
              <Col span={4}>
                <Row gutter={10}>
                  <Col span={24}>
                    <Form>
                      {handleSearch && (
                        <Form.Item>
                          <Input
                            size="small"
                            placeholder="Filter"
                            name="keyword"
                            id="keyword"
                            value={paginate.keyword}
                            onChange={handleSearch}
                            suffix={
                              <Tooltip title="Filter">
                                <SearchOutlined />
                              </Tooltip>
                            }
                          />
                        </Form.Item>
                      )}
                    </Form>
                  </Col>
                </Row>
              </Col>
              <Col span={20}>
                <Row gutter={6}>
                  <Col span={12}>
                    <Row>
                      <Col style={{ marginLeft: 2 }}>
                        <Tooltip title="Back">
                          <Button
                            type="primary"
                            size="small"
                            style={{ backgroundColor: "#f0ad4e" }}
                            onClick={handleBack}
                          >
                            <ArrowLeftOutlined />
                          </Button>
                        </Tooltip>
                      </Col>
                      {accessPermission.create
                        ? items && (
                            <Col style={{ marginTop: -4, marginLeft: 10 }}>
                              <Form.Item>
                                <Dropdown menu={{ items }}>
                                  <Button type="primary" size="small">
                                    <SettingOutlined />
                                    Action
                                    <DownOutlined />
                                  </Button>
                                </Dropdown>
                              </Form.Item>
                            </Col>
                          )
                        : items && (
                            <Col style={{ marginTop: -4, marginLeft: 10 }}>
                              <Form.Item>
                                <Button disabled type="primary" size="small">
                                  <SettingOutlined />
                                  Action
                                  <DownOutlined />
                                </Button>
                              </Form.Item>
                            </Col>
                          )}
                      <Col style={{ marginLeft: 10 }}>
                        <Tooltip title="Save">
                          <Button
                            type="primary"
                            onClick={handleSubmit}
                            size="small"
                            disabled={
                              isForms
                                ? !accessPermission.create &&
                                  !accessPermission.update
                                : true
                            }
                            onMouseEnter={onSetForms}
                          >
                            <SaveOutlined />
                          </Button>
                        </Tooltip>
                      </Col>
                      <Col style={{ marginLeft: 10 }}>
                        <Tooltip title="New">
                          <Button
                            type="primary"
                            style={{ backgroundColor: "#198754" }}
                            onClick={handleInsert}
                            size="small"
                            disabled={!accessPermission.create}
                          >
                            <EditOutlined />
                          </Button>
                        </Tooltip>
                      </Col>
                      {accessPermission.delete ? (
                        <Col style={{ marginLeft: 10 }}>
                          <Tooltip title="Delete">
                            <Button
                              type="primary"
                              size="small"
                              style={{ backgroundColor: "#ff4d4f" }}
                              onClick={handleRemove}
                              disabled={!id > 0}
                            >
                              <DeleteOutlined />
                            </Button>
                          </Tooltip>
                        </Col>
                      ) : (
                        <Col style={{ marginLeft: 10 }}>
                          <Tooltip title="Delete">
                            <Button
                              type="primary"
                              size="small"
                              style={{ backgroundColor: "#ff4d4f" }}
                              onClick={handleRemove}
                              disabled
                            >
                              <DeleteOutlined />
                            </Button>
                          </Tooltip>
                        </Col>
                      )}
                      {onClickDetail && id && (
                        <Col style={{ marginLeft: 10 }}>
                          <Tooltip title={labelDetail}>
                            <Button
                              size="small"
                              disabled={false}
                              onClick={onClickDetail}
                            >
                              {labelDetail}
                            </Button>
                          </Tooltip>
                        </Col>
                      )}
                    </Row>
                  </Col>
                  <Col span={12}>
                    <Row style={{ float: "right", marginRight: 15 }}>
                      <Col span={24} style={{ marginTop: 5 }}>
                        {showTableTogle ? (
                          <Tooltip title="Table View">
                            <Button
                              type="primary"
                              onClick={onTableTogle}
                              style={{
                                backgroundColor: "#198754",
                                marginRight: 10,
                              }}
                              size="small"
                            >
                              <InsertRowAboveOutlined />
                            </Button>
                          </Tooltip>
                        ) : null}
                        <Tooltip title="Refresh">
                          <a onClick={refresh} href="#reload" size="small">
                            <SyncOutlined />
                          </a>
                        </Tooltip>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Col>
            </Row>
            <Row gutter={15}>
              {!showTable ? (
                <Col span={4}>
                  <Card
                    style={{
                      marginTop: -15,
                      height: "62vh",
                      overflowY: "auto",
                    }}
                    className="data-master"
                  >
                    <InfiniteScroll
                      dataLength={data.length}
                      scrollableTarget="data-master"
                      hasMore={loading ? false : data.length < total}
                      loader={
                        <div
                          style={{
                            textAlign: "center",
                            marginTop: 10,
                          }}
                        >
                          <Button onClick={onLoadMore}>More...</Button>
                        </div>
                      }
                    >
                      <List
                        dataSource={data}
                        loading={loading}
                        renderItem={(item) => (
                          <List.Item
                            key={item.id}
                            style={{
                              cursor: "pointer",
                              backgroundColor:
                                item.id == id ? "#b0c4de71" : undefined,
                            }}
                          >
                            <div
                              style={{
                                width: "100%",
                              }}
                              onClick={(e) => {
                                onDetail(e, item.id);
                              }}
                            >
                              <List.Item.Meta
                                title={
                                  <div
                                    style={{
                                      marginLeft: 15,
                                    }}
                                  >
                                    {item.code}
                                  </div>
                                }
                                description={
                                  <div
                                    style={{
                                      marginLeft: 15,
                                    }}
                                  >
                                    {item.name}
                                  </div>
                                }
                              />
                            </div>
                          </List.Item>
                        )}
                      />
                    </InfiniteScroll>
                  </Card>
                </Col>
              ) : null}
              <Col span={showTable ? 24 : 20}>
                <Row>
                  <Col span={24} style={{ marginTop: -15 }}>
                    <Card className="content-cover" style={{ height: "62vh" }}>
                      <div className="content-detail">{children}</div>
                    </Card>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Card>
        </Col>
      </Row>
    </Card>
  );
};

export default CListData;
